
import { defineComponent, onMounted, onUnmounted, reactive } from "vue";
import TheForm from "@/components/TheForm.vue";
import TheInput from "@/components/TheInput.vue";
import TheButton from "@/components/TheButton.vue";
import { Log } from "@/model/log";
import { ShortCut, shortPacker } from "@rozbehsharahi/shortcuts";
import TheCard from "@/components/TheCard.vue";

export default defineComponent({
  components: { TheCard, TheButton, TheInput, TheForm },
  props: {
    log: {
      type: Log,
      required: false,
      default: new Log({}),
    },
  },
  setup(props, { emit }) {
    const state = reactive({
      draft: {
        identifier: props.log.getIdentifier(),
        content: props.log.getContent(),
        hours: props.log.getHours(),
      },
    });

    const methods = {
      commit() {
        emit("commit", new Log(state.draft));
      },
    };

    onMounted(() => {
      shortPacker.push([
        new ShortCut({
          label: "Commit",
          key: "Escape",
          action: () => emit("close"),
        }),
      ]);
    });

    onUnmounted(() => {
      shortPacker.pop();
    });

    return {
      state,
      methods,
    };
  },
});
